import UiCloseButton from './UiCloseButton.vue';
import UiIcon from './UiIcon.vue';

export default {
    name: 'ui-alert',

    props: {
        type: {
            type: String,
            default: 'info' // 'info', 'success', 'warning', or 'error'
        },
        removeIcon: {
            type: Boolean,
            default: false
        },
        dismissible: {
            type: Boolean,
            default: true
        }
    },

    computed: {
        classes() {
            return [
                `ui-alert--type-${this.type}`
            ];
        }
    },

    methods: {
        dismissAlert() {
            this.$emit('dismiss');
        }
    },

    components: {
        UiCloseButton,
        UiIcon
    }
};
